import React, { useContext, useEffect, useState } from "react"
import {Card, Loading, Header, Player} from "../components"
import Fuse from "fuse.js"
import * as ROUTES from "../constants/routes"
import {FirebaseContext} from "../context/firebase"
import FooterContainer from './footer'
import SelectProfileContainer from './profiles'

export function BrowseContainer({slides}) {
    const [category, setCategory] = useState('series');
    const [loading, setLoading] = useState(true)
    const [profile, setProfile] = useState({});
    const [searchTerm, setSearchTerm] = useState('');
    const [searchActive, setSearchActive] = useState(false)
    const [selectRows, setSelectRows] = useState([])

    const { firebase } = useContext(FirebaseContext)
    const user = firebase.auth().currentUser || {};

    // const user = {
    //     displayName: "Ali",
    //     photoURL: "1"
    // }
    
    useEffect(() => {
        setTimeout(() => {
            setLoading(false)
        }, 3000)
    }, [profile.displayName])

    useEffect(() =>{
        setSelectRows(slides[category])
    }, [slides, category])

    useEffect(() => {
        const fuse = new Fuse(selectRows, { keys: ['data.description', 'data.title', 'data.genre'] });
        const results = fuse.search(searchTerm).map(({ item }) => item);
        
        if (selectRows.length > 0 && searchTerm.length > 3 && results.length > 0) {
            setSelectRows(results);
        } else {
            setSelectRows(slides[category]);
        }
      }, [searchTerm])


    return profile.displayName ? (
        <>
            {loading ? <Loading src={user.photoURL} /> : <Loading.ReleaseBody />}

            <Header src='joker1' dontShowOnSmallViewPort>
                <Header.Frame>
                    <Header.Group>
                        <Header.Logo 
                            to={ROUTES.HOME}
                            src='images/misc/logo.svg'
                            alt="Netflix"
                        />
                        <Header.Link 
                            active={category === 'series' ? true : false}
                            onClick={() => setCategory('series')}
                        >
                            Series
                        </Header.Link>

                        <Header.Link 
                            active={category === 'films' ? true : false}
                            onClick={() => setCategory('films')}
                        >
                            Films
                        </Header.Link>
                        
                    </Header.Group>

                    <Header.Group>
                        <Header.Search>
                            <Header.SearchIcon
                                onClick={() => setSearchActive(!searchActive)}
                            >
                                <img  src="images/icons/search.png" alt="Search Icon" />
                            </Header.SearchIcon>
                            <Header.SearchInput 
                                value={searchTerm}
                                onChange={({ target }) => setSearchTerm(target.value)}
                                placeholder="Search Files and Series"
                                active={searchActive}
                            />
                        </Header.Search>

                        <Header.Profile>
                            <Header.Picture src={user.photoURL} />
                            <Header.Dropdown>
                                <Header.Group>
                                    <Header.Picture src={user.photoURL} />
                                    <Header.Link src={user.displayName} />
                                </Header.Group>
                                <Header.Group>
                                    <Header.Link onClick={() => firebase.auth().signOut()}>Sign Out</Header.Link>
                                </Header.Group>
                            </Header.Dropdown>
                        </Header.Profile>

                    </Header.Group>

                </Header.Frame>


            <Header.Feature>
                <Header.FeatureCallOut>Watch Joker Now</Header.FeatureCallOut>
                <Header.Text>
                    Forever alone in a crowd, failed comedian Arthur Fleck seeks connection as he walks the streets of Gotham
                    City. Arthur wears two masks -- the one he paints for his day job as a clown, and the guise he projects in a
                    futile attempt to feel like he's part of the world around him.
                </Header.Text>
                <Header.PlayButton>Play</Header.PlayButton>
            </Header.Feature>

            </Header>

            <Card.Group>
                {selectRows.map((slideItem) => (
                    <Card key={`${category} - ${slideItem.title.toLowerCase()}`}>
                        <Card.Title>{slideItem.title}</Card.Title>
                        <Card.Entities>
                            {slideItem.data.map((item) => (
                                <Card.Item key={item.docId} item={item}>
                                    <Card.Image src={`images/${category}/${item.genre}/${item.slug}/small.jpg`} />
                                    <Card.Meta>
                                        <Card.SubTitle>{item.title}</Card.SubTitle>
                                        <Card.Text>{item.description}</Card.Text>
                                    </Card.Meta>
                                </Card.Item>
                            ))}
                        </Card.Entities>

                        <Card.Feature category={category}>
                            <Player>
                                <Player.Button />
                                <Player.Video src="videos/bunny.mp4" />
                            </Player>
                        </Card.Feature>
                    </Card>
                ))}
            </Card.Group>


            <FooterContainer />
        </> )
        : ( <SelectProfileContainer user={user} setProfile={setProfile} /> )
}