import React, {useState, createContext, useContext} from "react"
import { Container, Inner, Title, Header, Body, Item, Frame } from "./styles/accordian"

const ToggleContext = createContext();

export default function Accordian({children, ...restProps}) {
    return (
        <Container {...restProps}>
            <Inner>{children}</Inner>
        </Container>
    )
}

Accordian.Item = function AccordianItem({children, ...restProps}) {
    const [toggle, setToggle] = useState(false)

    return (
        <ToggleContext.Provider value={{toggle, setToggle}}>
            <Item {...restProps}>{children}</Item>
        </ToggleContext.Provider>
    ) 
}

Accordian.TItle = function AccordianTitle({children, ...restProps}) {
    return <Title {...restProps}>{children}</Title>
}

Accordian.Header = function AccordianTitle({children, ...restProps}) {
    const {toggle, setToggle} = useContext(ToggleContext)

    return (
        <Header onClick={() => setToggle(!toggle)} {...restProps}>{children}
        {toggle ? 
            <img src="images/icons/close-slim.png" alt="close" />
        : 
            <img src="images/icons/add.png" alt="Open" />
        }
        </Header>
    )
}

Accordian.Body = function AccordianBody({children, ...restProps}) {
    const {toggle} = useContext(ToggleContext)

    return toggle ? <Body {...restProps}>{children}</Body> : null;
}

Accordian.Frame = function AccordianFrame({...restProps}) {
    return <Frame {...restProps} />
}